import { RouterProvider, Outlet, createHashRouter, Route, createRoutesFromElements } from 'react-router-dom'
import { Box, Grommet } from 'grommet';
import { Home, Navbar, Profile, Register, Welcome } from './components/index'
import { AuthProvider, useSession } from './useSession'

const routing = createHashRouter(
  createRoutesFromElements(
    <Route path='/' element={<Layout />}>
      <Route path='/' element={<Home />} />
      <Route path='/profile' element={<Profile />} />
      <Route path='/register' element={<Register />} />
      <Route path='/welcome' element={<Welcome />} />
    </Route>
  )
)

function App() {
  const { session } = useSession()
  return (
    <AuthProvider>
      <RouterProvider router={routing} session={session} />
    </AuthProvider>
  );
}

export default App;

function Layout () {
  return (
    <Grommet full>
      <Box fill>
        <Navbar />
        <Outlet />
      </Box>
    </Grommet>
  )
}